const simplifyUrl = (url) => {//replaceåˆ é™¤åŸæ¥çš„å¹¶æ›¿æ¢æˆæ–°çš„
    return url.replace('https://', '')
        .replace('http://', '')
        .replace('www.', '')
        .replace(/\/.*/, '')//æ­£åˆ™è¡¨è¾¾å¼ï¼šåˆ é™¤ / å¼€å¤´çš„å†…å®¹--é˜²æ­¢ç”¨æˆ·è¾“å…¥è¿‡å¤šçš„urlåç¼€
}
//ä¼˜åŒ–ä»£ç //å°è£…//å…ˆåˆ é™¤ä¹‹å‰çš„åœ¨å‚¨å­˜æ–°çš„
const render = () => {
    $siteList.find('li:not(.last)').remove()//æ‰¾åˆ°æ‰€æœ‰çš„nodeç½‘å€ï¼Œå”¯ç‹¬ä¸ç€æœ€åä¸€ä¸ªï¼ˆæ–°ç”Ÿæˆï¼‰è¿›è¡Œæ¸…ç©ºï¼Œæ–°ç”Ÿæˆä¸èƒ½åˆ é™¤ï¼
    hashMap.forEach((node, index) => {//åˆ é™¤åŠŸèƒ½éœ€è¦ç´¢å¼•å€¼ï¼Œé€šè¿‡forEachä¼ å…¥ç¬¬äºŒä¸ªå‚æ•°idnexï¼Œè·å–ç´¢å¼•
        // console.log(index)//æ‰“å°å½“å‰ç½‘é¡µçš„ç´¢å¼•å€¼
        const $li = $(`<li>        
                    <div class="site">
                        <div class="logo">${node.logo}</div>
                        <div class="link">${simplifyUrl(node.url)}</div>
                        <div class='close'>
                          <svg class="icon">//ç›‘å¬ç‚¹å‡»ï¼Œé˜»æ­¢å†’æ³¡
                             <use xlink:href="#icon-close"></use>
                          </svg>                      
                        </div>
                    </div>                
            </li>`).insertBefore($lastLi)

        $li.on('click', () => {//ä»£æ›¿<a>æ ‡ç­¾
            window.open(node.url)//$lié‡Œçš„<a href="${node.url}"></a>åˆ é™¤ï¼Œè·³è½¬é¡µé¢node.urlçš„æ“ä½œé€šè¿‡winodw.openæ‰§è¡Œ//åŸå› <a>æ ‡ç­¾å¤ªç²—ç³™äº†ï¼Œä¸çµæ•ï¼Œé€šè¿‡jsæ§åˆ¶æ›´å¥½
        })

        $li.on('click', '.close', (e) => {//é˜»æ­¢å†’æ³¡é˜²æ­¢è§¦å‘è·³è½¬äº‹ä»¶
            console.log('é˜»æ­¢')
            e.stopPropagation()//js é˜»æ­¢å†’æ³¡é˜»æ­¢è·³è½¬ 
            console.log(hashMap)
            //æ‹¿åˆ°indexç´¢å¼•åï¼Œåˆ é™¤æ•°ç»„éœ€è¦é€šè¿‡splice()æ–¹æ³•
            hashMap.splice(index, 1)
            render()//åˆ æ‰ä¹‹åéœ€è¦é‡æ–°æ¸²æŸ“ï¼ä¸ç„¶ä¸ä¼šæ˜¾ç¤º
        })
    })
}
render()

